<template>
  <div class="container">
    <div class="form">
      <el-form label-width="160px" :model="queryData" :rules="rules" :inline="true" ref="queryForm">
        <el-form-item label="检视类型" prop="type">
          <el-select v-model="queryData.type" placeholder="请选择检视类型" @change="typeChange">
            <el-option
              v-for="item in typeList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属机构" prop="org">
          <el-select v-model="queryData.org" placeholder="请选择所属机构">
            <el-option
              v-for="item in orgList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属渠道" prop="channel">
          <el-select v-model="queryData.channel" placeholder="请选择所属渠道">
            <el-option
              v-for="item in channelList"
              :key="item.code"
              :value="item.code"
              :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item :label="showLabel" prop="version">
          <el-input v-model="queryData.version"></el-input>
        </el-form-item>
        <el-form-item label="起始日期" prop="startDate">
          <el-date-picker
            type="date"
            placeholder="请选择起始日期"
            v-model="queryData.startDate"
            style="width: 100%;">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="截止日期" prop="endDate">
          <el-date-picker
            type="date"
            placeholder="请选择截止日期"
            v-model="queryData.endDate"
            style="width: 100%;">
          </el-date-picker>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button plain type="primary" size="small" @click="resetForm">重置</el-button>
      <el-button plain type="primary" size="small" @click="searchData">查询</el-button>
      <el-button plain type="primary" size="small" @click="exportData">导出</el-button>
    </div>
    <div class="data" :hidden.sync="dataHidden">
      <div class="table">
        <el-table style="width: 100%"  :data="curData" stripe :header-cell-style="tableHeader">
          <el-table-column
            header-align="center"
            align="center"
            v-for="column in columns"
            :key="column.prop"
            :prop="column.prop"
            :label="column.label"
            :min-width="column.width"
            v-if="column.isShow">
          </el-table-column>
        </el-table>
        <base-pagination
          :total="data.length"
          @dataChange="dataChange">
        </base-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const BasePagination = () => import('@/components/BasePagination')
export default {
  name: 'Price',
  components: {BasePagination},
  data () {
    return {
      dataHidden: true,
      queryData: {
        type: '',
        org: '50000',
        channel: '001',
        version: '50000201801010900',
        startDate: '2018-01-01',
        endDate: '2018-03-01'
      },
      rules: {
        type: [{required: true, trigger: 'change', message: '检视类型不能为空'}],
        org: [{required: true, trigger: 'change', message: '所属机构不能为空'}],
        channel: [{required: true, trigger: 'change', message: '所属渠道不能为空'}],
        startDate: [{required: true, trigger: 'blur', message: '起始日期不能为空'}],
        endDate: [{required: true, trigger: 'blur', message: '截止日期不能为空'}]
      },
      showLabel: '定价策略版本号',
      typeList: [
        {code: 'person', name: '定价策略预测对比-个车'},
        {code: 'protocol', name: '定价策略预测对比-协议'}
      ],
      orgList: [
        {code: '10000', name: '北京分公司'},
        {code: '20000', name: '上海分公司'},
        {code: '30000', name: '江苏省分公司'},
        {code: '40000', name: '山东省分公司'},
        {code: '50000', name: '山西省分公司'}
      ],
      channelList: [
        {code: '001', name: '传统渠道'},
        {code: '002', name: '网电渠道'}
      ],
      columns: [
        {prop: 'priceVersion', label: '定价策略版本号', width: 120, isShow: false},
        {prop: 'bizSector', label: '业务板块', width: 120, isShow: false},
        {prop: 'bizType', label: '业务小类', width: 120, isShow: false},
        {prop: 'protocolNo', label: '协议号', width: 120, isShow: false},
        {prop: 'protocolName', label: '协议名称', width: 120, isShow: false},
        {prop: 'inspectIndex', label: '检视指标', width: 120, isShow: true},
        {prop: 'inspectResult', label: '检视结果', width: 120, isShow: true},
        {prop: 'inspectDate', label: '检视日期', width: 120, isShow: true}
      ],
      currentPage: 1,
      pageSize: 5,
      data: [],
      personData: [
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-新车',
          bizType: '单交,单商_单三_无附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算200万',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-新车',
          bizType: '单交,单商_单三_有附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算5%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-新车',
          bizType: '单交,单商_损三_无附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '经营成本率-对比差值',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-旧车',
          bizType: '单交,单商_损三_有附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '市场费用率-实际值',
          inspectResult: '超过预算10%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-旧车',
          bizType: '单交,交商_交三_无附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算100万',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '50000201801010900',
          bizSector: '家用-旧车',
          bizType: '单交,交商_交三_有附加',
          protocolNo: '',
          protocolName: '',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        }
      ],
      protocolData: [
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101010001',
          protocolName: '2018机动车辆投保协议',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算200万',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101020001',
          protocolName: '2018非机动车辆投保协议',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算5%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101030001',
          protocolName: '2018营业出租客车投保协议',
          inspectIndex: '经营成本率-对比差值',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101040001',
          protocolName: '2018营业租赁客车投保协议',
          inspectIndex: '市场费用率-实际值',
          inspectResult: '超过预算10%',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101050001',
          protocolName: '2018非营业企业客车投保协议',
          inspectIndex: '保费收入-实际值',
          inspectResult: '超过预算100万',
          inspectDate: '2018-01-21'
        },
        {
          priceVersion: '',
          bizSector: '',
          bizType: '',
          protocolNo: '20180101060001',
          protocolName: '2018非营业企业货车投保协议\n',
          inspectIndex: '保费收入占比',
          inspectResult: '超过预算3%',
          inspectDate: '2018-01-21'
        }
      ]
    }
  },
  methods: {
    typeChange (val) {
      this.currentPage = 1
      if (val === 'person') {
        this.showLabel = '定价策略版本号'
        this.columns.forEach(item => {
          if (item.prop === 'priceVersion' || item.prop === 'bizSector' || item.prop === 'bizType') {
            item.isShow = true
          } else if (item.prop === 'protocolNo' || item.prop === 'protocolName') {
            item.isShow = false
          }
        })
        this.data = this.personData
        this.dataHidden = false
      } else if (val === 'protocol') {
        this.showLabel = '协议号'
        this.columns.forEach(item => {
          if (item.prop === 'priceVersion' || item.prop === 'bizSector' || item.prop === 'bizType') {
            item.isShow = false
          } else if (item.prop === 'protocolNo' || item.prop === 'protocolName') {
            item.isShow = true
          }
        })
        this.data = this.protocolData
        this.dataHidden = false
      } else {
        this.showLabel = '定价策略版本号'
        this.columns.forEach(item => {
          if (item.prop === 'priceVersion' || item.prop === 'bizSector' || item.prop === 'bizType') {
            item.isShow = true
          } else if (item.prop === 'protocolNo' || item.prop === 'protocolName') {
            item.isShow = false
          }
        })
        this.data = []
        this.dataHidden = true
      }
    },
    resetForm () {
    },
    searchData () {
      this.$refs['queryForm'].validate(valid => {
        if (valid) {
          this.currentPage = 1
          if (this.queryData.type === 'person') {
            this.columns.forEach(item => {
              if (item.prop === 'priceVersion' || item.prop === 'bizSector' || item.prop === 'bizType') {
                item.isShow = true
              } else if (item.prop === 'protocolNo' || item.prop === 'protocolName') {
                item.isShow = false
              }
            })
            this.data = this.personData
            this.dataHidden = false
          } else if (this.queryData.type === 'protocol') {
            this.columns.forEach(item => {
              if (item.prop === 'priceVersion' || item.prop === 'bizSector' || item.prop === 'bizType') {
                item.isShow = false
              } else if (item.prop === 'protocolNo' || item.prop === 'protocolName') {
                item.isShow = true
              }
            })
            this.data = this.protocolData
            this.dataHidden = false
          } else {
            this.data = []
            this.dataHidden = true
          }
        }
      })
    },
    exportData () {
    },
    dataChange (size, page) {
      this.pageSize = size
      this.currentPage = page
    }
  },
  computed: {
    curData () {
      const result = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end > this.data.length ? this.data.length : end
      for (let i = start; i < end; ++i) {
        result.push(this.data[i])
      }
      return result
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  }
}
</script>

<style scoped>

</style>



